<template>

  <div>
    <div v-for="item in images">
      <div class="row">
        <div class="my-2 col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12" v-for="img in item">
          <vue-picture-swipe :items="img"></vue-picture-swipe>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  import VuePictureSwipe from 'vue-picture-swipe';

  export default {
    data() {
      return {
        name_of_images: [],
        images: [],
        index: 1,
        max_index: 0,
        on: true,
        row: [],
        h: 0,
        w: 0
      }
    },
    methods: {
      row_pushing() {
        this.row.push([{
          src: '/get_src/' + this.name_of_images[this.index-1],
          thumbnail: '/get_thumbnail/' + this.name_of_images[this.index-1],
          w: this.w,
          h: this.h
        }]);
      },
      async loadMoreImages() {
        if (this.max_index != 0) {
          for (var i = 1; i <= 20; i++) {
            if (this.on) {
              if (this.index % 4 != 0) {
                this.row_pushing();
              } else {
                this.row_pushing();
                this.images.push(this.row);
                this.row = [];
              }
              if (this.index == this.max_index) {
                if (this.row != []) {
                  this.images.push(this.row);
                }
                this.on = false;
                window.removeEventListener('scroll', e=>{
                  var scrollHeight = Math.max(
                    document.body.scrollHeight, document.documentElement.scrollHeight,
                    document.body.offsetHeight, document.documentElement.offsetHeight,
                    document.body.clientHeight, document.documentElement.clientHeight
                  );
                  if (window.innerHeight + window.scrollY + 50 >= scrollHeight) {
                    this.loadMoreImages();
                  }
                });
              } else {
                this.index = this.index + 1;
              }
            }
          }
        }
      }
    },
    components: {
      'vue-picture-swipe': VuePictureSwipe
    },
    async mounted() {
      const response = await fetch('/get_images'); 
      this.name_of_images = await response.json();
      this.max_index = this.name_of_images.length;

      this.loadMoreImages();
      window.addEventListener('scroll', e=>{
        var scrollHeight = Math.max(
          document.body.scrollHeight, document.documentElement.scrollHeight,
          document.body.offsetHeight, document.documentElement.offsetHeight,
          document.body.clientHeight, document.documentElement.clientHeight
        );
        if (window.innerHeight + window.scrollY >= scrollHeight) {
          this.loadMoreImages();
        }
      });
    }
  };

</script>

