<template>
  <div class="">
      <form
        @submit="checkForm"
        action="/"
        method="post"
        enctype=multipart/form-data
      >
      <div>
          <div class="file-upload-form">
              Загрузите картинки:
              <input class="btn btn-success" type="file" multiple ref="file_input" @change="uploadFiles" name="IMAGES">
          </div>
          <br>
          <input
            type="submit"
            value="Отправить"
            class="btn btn-primary"
          >
      </div>
      </form>
  </div>
</template>
<script>

  export default {
    data() {
      return {
        imageData: "",
      };
    },
    methods: {
        uploadFiles () {
          let files = this.$refs.file_input.files
          let data = new FormData
          for (let i = 0; i < files.length; i++) {
             data.append('input_name[]', files[i])
          }
        }
    }
  };
</script>
